package org.jetbrains.webwiz.models

enum class KmpTarget(
    val targetName: String,
    val userName: String,
    val mainSourceSetName: String,
    val testSourceSetName: String
) {
    JVM("jvm", "JVM", "jvmMain","jvmTest"),
    ANDROID("android", "Android", "androidMain","androidUnitTest"),
    JS("js", "JS", "jsMain","jsTest"),
    IOS("ios", "iOS", "iosMain","iosTest"),
    TV_OS("tvos", "tvOS", "tvosMain","tvosTest"),
    WATCH_OS("watchos", "watchOS", "watchosMain","watchosTest"),
    LINUX("linux", "Linux", "linuxMain","linuxTest"),
    MACOS("macos", "macOS", "macosMain","macosTest"),
    WINDOWS("windows", "Windows", "windowsMain","windowsTest")
}

private val NativeTargets = setOf(
    KmpTarget.LINUX,
    KmpTarget.MACOS,
    KmpTarget.IOS,
    KmpTarget.TV_OS,
    KmpTarget.WATCH_OS,
    KmpTarget.WINDOWS
)

fun KmpTarget.isJvm() = this in setOf(KmpTarget.JVM, KmpTarget.ANDROID)
fun KmpTarget.isNative() = this in NativeTargets

fun Set<KmpTarget>.isNativeTargets() = this.all { it in NativeTargets }
fun ProjectInfo.isCommonNativeTargetPresent() = targets.filter { it in NativeTargets }.size > 1
fun ProjectInfo.isSingleNativeTargetPresent() = targets.filter { it in NativeTargets }.size == 1
